@import '../../sass/variables.scss';


.search-popup {
    &__container {
        top: 0;
        min-width: 900px;
        height: 600px;
        z-index: 100;
        background-color: #fff;
        box-shadow: 3.4px 3.4px 2.7px rgba(0, 0, 0, 0.009), 8.7px 8.7px 6.9px rgba(0, 0, 0, 0.013),
            17.7px 17.7px 14.2px rgba(0, 0, 0, 0.017), 36.5px 36.5px 29.2px rgba(0, 0, 0, 0.021),
            100px 100px 80px rgba(0, 0, 0, 0.03);
        border-radius: 10px;
        overflow: hidden;
        color: $darkGray;
    }

    &__top {
        display: flex;
        align-items: center;
        height: 60px;
        width: 100%;
        padding: 0 10px 0 10px;

        i {
            padding: 0 15px;
            padding-left: 5px;
            font-size: 20px;
            color: $lightGray;
        }
    }

    &__input {
        height: 50px;
        width: 90%;
        font-size: 20px;
        color: $darkGray;
        border: none;
        outline: none;
    }

    &__content {
        padding: 10px;
        display: flex;
        flex-direction: column;
        gap: 10px;
        height: 510px;
        overflow: scroll;

        &::-webkit-scrollbar {
            display: none;
        }

        a {
            text-decoration: none;
            color: $darkGray;
        }
    }

    &__item {
        border-radius: 5px;
        display: flex;
        align-items: center;
        cursor: pointer;
        height: 40px;
        transition: 0.3s all ease 0s;
        padding: 0 5px;

        &:hover {
            background-color: $hoverGray;
        }
    }

    &__title {
        font-size: 17px;
    }

    &__photo {
        font-size: 20px;
        margin-right: 10px;
    }
}