@import './../../sass/variables.scss';

.sidebar {
    width: 100%;
    height: 100vh;
    overflow: hidden;
    flex-shrink: 0;
    background-color: #fcfafa;

    &__paper {
        width: 100%;
    }
}

.sidebar-user {
    display: flex;
    align-items: center;
    gap: 10px;
    font-size: 18px;
    color: $darkGray;
    cursor: pointer;
    padding-left: 10px;
    padding-top: 10px;
    width: 100%;

    &:hover {
        .sidebar-popup {
            display: block;
            visibility: visible;
        }
    }

    &__avatar {
        width: 25px;
        height: 25px;
        background-color: $darkGray;
        border-radius: 50%;
    }
}

.sidebar-popup {
    position: absolute;
    visibility: hidden;
    display: none;
    top: 30px;
    left: 0;
    width: 100%;
    padding: 0 5%;

    &__content {
        position: relative;
        background-color: #fff;
        transition: .2s all ease 0s;
        border-radius: 10px;
        box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
        position: relative;
        width: 100%;

    }

    .main-settigs {
        padding: 10px;
        padding-bottom: 5px;

        li {
            font-size: 18px;
            padding: 5px;
            box-shadow: none;
            list-style-type: none;
            cursor: pointer;
            border-radius: 5px;
            transition: 0.3s all ease 0s;
            margin-bottom: 5px;

            &:hover {
                background-color: $hoverGray;
            }
        }
    }
}

.sidebar-header {
    position: relative;
    height: 120px;
    width: 100%;
}

.sidebar-content {
    overflow: auto;
    overflow-x: hidden;
    padding: 0 10px;
    height: calc(100vh - 140px);

    /* width */
    &::-webkit-scrollbar {
        width: 7px;
    }

    /* Track */
    &::-webkit-scrollbar-track {
        border-radius: 5cap;
    }

    /* Handle */
    &::-webkit-scrollbar-thumb {
        background: $scrollGray;
        border-radius: 5px;
    }
}

.sidebar-item-main {
    padding-top: 15px;
}

.sidebar-item {
    display: flex;
    flex-direction: column;
    width: 100%;

    ul {
        margin: 0;
        padding: 0;
        margin-bottom: 5px;
    }

    &:not(:last-child) {
        margin-bottom: 10px;
    }

    p {
        font-size: 15px;
        color: $lightGray;
    }

    .sidebar-button {
        margin: 0;
        padding: 0;
        margin-bottom: 15px;
        padding-left: 10px;
        color: #504d4d;
        font-size: 16px;
        width: 100%;
        cursor: pointer;
        font-weight: 500;
        display: flex;
        align-items: center;
        gap: 10px;
    }
}

.page-sidebar-item {
   
    &__container {
        position: relative;

        &:hover {
            background-color: $hoverGray;
            border-radius: 5px;

            .fa-trash,
            .fa-plus,
            .fa-star {
                visibility: visible;
            }

            .page-actions {
                background-color: $hoverGray;
            }
        }

        &.opened {
            background-color: $hoverGray;
            border-radius: 5px;
        }

        &.active {
            .fa-chevron-right {
                transform: rotate(90deg);
            }
        }

        text-decoration: none;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 0 5px 0 6px;
        overflow: hidden;
    }

    &__loader {
        display: flex;
    }

    &__main {
        display: flex;
        align-items: center;
        width: 100%;
    }

    &__children {
        padding-left: 25px;
        padding-top: 5px;
    }

    &__empty {
        padding: 5px 0;
        white-space: nowrap;
    }

    .fa-trash,
    .fa-plus,
    .fa-star {
        font-size: 14px;
        color: #000;
        cursor: pointer;
        visibility: hidden;
    }

    .fa-star {
        color: $goldColor;
    }

    .fa-chevron-right {
        font-size: 15px;
        color: #000;
        cursor: pointer;
        margin-right: 5px;
        border-radius: 5px;
        transition: 0.2s all ease 0s;
    }
}


.page-actions {
    display: flex;
    align-items: center;
    gap: 10px;
    position: absolute;
    right: 0;
    padding-right: 10px;
    padding-left: 10px;
    height: 100%;
}

.page-link {
    display: flex;
    align-items: center;
    gap: 10px;
    width: 100%;
    padding: 2px;

    a {
        text-decoration: none;
        color: $darkGray;
        width: 100%;
        display: block;
    }

    &__name {
        white-space: nowrap;
    }

    &__photo {
        width: 22px;
    }

    &:hover {}
}