@import '../../sass/variables.scss';


.page {
    overflow: auto;
    overflow-x: hidden;
    height: 100vh;
    padding: 0 15%;
    padding-top: 15vh;
    position: relative;

    &.full-width {
        padding: 0 4% 0 7%;
        padding-top: 15vh;
    }

    &.locked {
        &::before {
            content: "";
            display: block;
            width: 100%;
            height: calc(100% - 15vh);
            z-index: 51;
            background-color: red;
            opacity: 0;
            position: absolute;
        }
    }

    /* width */
    &::-webkit-scrollbar {
        width: 9px;
    }

    /* Track */
    &::-webkit-scrollbar-track {
        border-radius: 5cap;
    }

    /* Handle */
    &::-webkit-scrollbar-thumb {
        background: $scrollGray;
        border-radius: 5px;
    }

    &__content {
        padding-bottom: 150px;
    }

    &__element {

    }

    &__element--dragging {
        opacity: 0.5;
    }

    &__icon {
        font-size: 45px;
        margin-bottom: 10px;
        cursor: pointer;
        position: relative;

        &:hover {
            background: $hoverGray;
            border-radius: 5px;
        }
    }

    &__top {
        margin-bottom: 40px;
    }

    &__title {
        textarea {
            border: none;
            outline: none; 
            width: 100%;
            resize: none;
            font-size: 55px;
            padding: 0;
            background-color: #fff;
        }
    }

    &__loader {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
    }
}