@import './variables.scss';

* {
    box-sizing: border-box;
}

body {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Encode Sans', sans-serif;
}
.app-flexboard-resizer {
    background-color: #fcfafa !important;
}

.loader-spinner {
    border: 4px solid $hoverGray;
    border-top: 4px solid $lightGray;
    border-radius: 50%;
    animation: spin 1s linear infinite;

    @keyframes spin {
        0% {
            transform: rotate(0deg);
        }

        100% {
            transform: rotate(360deg);
        }
    }
}

.loader-container {
    display: flex;
    align-items: center;
    justify-content: center;
}









.my-popup {
    &__bg {
        position: fixed;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        z-index: 20;
        display: none;

        &.open {
            display: block;
        }
    }

    .popup {
        z-index: 20;
        position: relative;
        display: inline-block;

        &.open {
            .popup-content {
                visibility: visible;
            }
        }

        .popup-trigger {
            cursor: pointer;
        }

        .popup-content {
            position: absolute;
            visibility: hidden;
            color: black;
            padding: 8px;
            border-radius: 4px;
            white-space: nowrap;
            z-index: 100;
            background-color: #fff;
            box-shadow: 3.4px 3.4px 2.7px rgba(0, 0, 0, 0.009), 8.7px 8.7px 6.9px rgba(0, 0, 0, 0.013),
                17.7px 17.7px 14.2px rgba(0, 0, 0, 0.017), 36.5px 36.5px 29.2px rgba(0, 0, 0, 0.021),
                100px 100px 80px rgba(0, 0, 0, 0.03);
            border-radius: 10px;
            padding: 20px;
            padding-right: 10px;
            top: 0;
            left: 60px;
            width: 400px;
            height: 400px;
            overflow: auto;

            &__items {
                display: flex;
                flex-wrap: wrap;
            }

            &__item {
                flex-basis: 10%;
                margin-bottom: 5px;
                font-size: 18px;
                cursor: pointer;
                text-align: center;

                &:hover {
                    background: $lightGray;
                    border-radius: 5px;
                }
            }

            /* width */
            &::-webkit-scrollbar {
                width: 7px;
            }

            /* Track */
            &::-webkit-scrollbar-track {
                border-radius: 5cap;
            }

            /* Handle */
            &::-webkit-scrollbar-thumb {
                background: #99999979;
                border-radius: 5px;
            }
        }
    }
}







.container {
    overflow: hidden;
}

.content {
    position: relative;
}

.custom-textarea {
    textarea {
        border: none;
        outline: none; // Add this line to remove the default outline
        width: 95%;
        height: 100%;
        resize: none;
        font-size: 20px;
        padding: 0;
        overflow: hidden;
        background-color: #fff;
        border: 1px solid red;
        padding-left: 50px;

        &:focus::placeholder {
            color: #ccc;
        }

        &:not(:focus)::placeholder {
            color: transparent;
        }
    }
}

