//     margin-top: 20vh !important;
// }


.popup-overlay {
    background-color: rgba(0, 0, 0, 0.3);
}


.cancel-popup {
    position: fixed;
    right: 30px;
    bottom: 30px;
    background: $darkGray;
    z-index: 20;
    border-radius: 10px;
    font-size: 17px;
    display: flex;
    align-items: center;
    gap: 20px;
    transition: 0.3s all ease 0s;
    height: 50px;
    padding: 0 20px;
    color: #fff;

    &__button {
        cursor: pointer;
    }
}

.sidebar-popup {
    .css-ll5g6i {
        box-shadow: none;
    }

   
}

.app-flexboard-content {
    overflow: hidden;
    width: auto;
}

.user-form {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;

    &__link {
        color: $darkGray;
        text-decoration: none;
    }
            
    &__button {
        width: 100%;
        height: 40px;
        outline: none;
        border: none;
        border-radius: 5px;
        background-color: $scrollGray;
        color: $darkGray;
        font-size: 18px;
        cursor: pointer;
    }
}

.google-auth-button {
    width: 100%;
    margin: 10px 0;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    background-color: #fff;
    border-radius: 5px;
    border: 1px solid $darkGray;

    img {
        width: 35px;
    }
}