@import '../../sass/variables.scss';

.page-element {
    width: 100%;
    color: $darkGray;
    display: flex;
    align-items: center;
    position: relative;

    &::before {
        position: absolute;
        content: '';
        z-index: 2;
        display: block;
        right: 100%;
        top: 5px;
        height: 20px;
        width: 60px;
    }

    &:hover,
    &:focus {
        .page-element__controll {
            visibility: visible;
        }
        .textarea-element::placeholder {
            color: #888888;
        }
    }

    &__controll {
        display: flex;
        align-items: center;
        justify-content: center;
        visibility: hidden;
        position: absolute;
        top: 0;
        right: 100%;
        z-index: 3;

        &.Heading1 {
            top: 12px;
        }

        &.Heading2 {
            top: 3px;
        }
    }

    &__actions {
        display: flex;
        align-items: center;
        border-radius: 5px;
        transform: translate(0, 4px);

        i {
            color: #888888;
            font-size: 17px;
            cursor: pointer;
            transition: .2s all ease 0s;
            padding: 4px;
            border-radius: 5px;

            &:hover {
                background-color: $hoverGray;
            }

            &.fa-grip {
                cursor: move;
            }
        }
    }

    &__content {
        border-radius: 5px;
        transition: .2s all ease 0s;
        width: 100%;
        padding: 3px;
        height: auto;
    }

}

.page-element-add {
    position: relative;
    z-index: 5;

    &.active {
        .page-element-add__container {
            display: block;
        }
    }

    &__container {
        display: none;
        position: absolute;
        width: 300px;
        height: 300px;
        left: 0;
        bottom: 0;

        &.bottom {
            top: 0;
            bottom: auto;
        }

    }

    &__popup {
        position: absolute;
        background-color: #fff;
        width: 300px;
        height: 310px;
        left: 52px;
        bottom: 0;
        border-radius: 5px;
        box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
        overflow-y: auto;
        padding: 5px;
        z-index: 20;

        /* width */
        &::-webkit-scrollbar {
            width: 7px;
        }

        /* Track */
        &::-webkit-scrollbar-track {
            border-radius: 5cap;
        }

        /* Handle */
        &::-webkit-scrollbar-thumb {
            background: $scrollGray;
            border-radius: 5px;
        }
    }

    &__section {
        margin-bottom: 20px;
    }

    &__caption {
        color: $lightGray;
        font-size: 13px;
        margin-bottom: 8px;
    }

    &__item {
        border-radius: 5px;
        transition: .2s all ease 0s;
        display: flex;
        margin-bottom: 5px;
        gap: 10px;
        padding: 5px;
        cursor: pointer;

        &:hover {
            background-color: $hoverGray;
        }
    }

    &__image {
        width: 40px;
        height: 40px;
        border-radius: 5px;
    }

    &__name {
        margin-bottom: 5px;

    }

    &__description {
        color: $lightGray;
        font-size: 14px;
    }
}

.textarea-element {
    outline: none;
    border: none;
    resize: none;
    font-size: 18px;
    color: $darkGray;
    height: 100%;
    background-color: transparent;
    width: 100%;
    overflow: hidden;
    box-sizing: border-box;

    &.visible {
        &::placeholder {
            color: #888888;
        }
    }

    &::placeholder {
        color: transparent;
    }

    &:focus, &:hover {
        &::placeholder {
            color: #888888;
        }
    }
}

.heading-element {
    width: 100%;
    border: none;
    outline: none;
    resize: none;
    overflow: hidden;
    color: $darkGray;
    box-sizing: border-box;
    background-color: transparent;

    &.big {
        font-size: 40px;
    }

    &.medium {
        font-size: 25px;
    }
}

.to-do-element {
    display: flex;
    align-items: start;
    height: 100%;

    &__checkbox {
        outline: none;
        width: 16px;
        height: 16px;
        transform: translate(0, 2px);
        cursor: pointer;
    }

    &__description {
        outline: none;
        border: none;
        font-size: 18px;
        color: $darkGray;
        resize: none;
        overflow: hidden;
        width: 95%;
        height: 100%;

        &.crossed-out {
            text-decoration: line-through;
        }
    }
}

.page-link-element {
    width: 100%;
    display: flex;
    align-items: center;
    color: $darkGray;
    text-decoration: none;
    gap: 6px;

    &__ico {
        font-size: 20px;
    }

    &__title {
        font-size: 18px;
        text-decoration: underline;
    }
}

.code-block-element {
    position: relative;
    background-color: #F7F7F7;
    padding-top: 40px;
    margin-bottom: 10px;

    &__select {
        width: 140px;
        top: 10px;
        left: 15px;
        position: absolute;
        height: 50px;
        z-index: 2;
    }

    &__copy {
        position: absolute;
        top: 10px;
        right: 15px;
        z-index: 2;
        font-size: 17px;
        cursor: pointer;
        transition: .2s all linear 0s;
        padding: 5px;
        color: $darkGray;
        border-radius: 5px;

        &:hover {
            background-color: #dcd5d5;
        }
    }
}