@import './../../sass/variables.scss';

.page-navbar {
    width: 100%;
    position: absolute;
    top: 0;
    right: 0;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    z-index: 52;
    padding-right: 15px;
    background-color: #fff;

    &__action {
        min-width: 35px;
        height: 35px;
        border-radius: 5px;
        display: flex;
        align-items: center;
        justify-content: center;
        transition: .3s all ease 0s;
        cursor: pointer;
        &:hover {
            background-color: $hoverGray;
        }
    }

    i {
        font-size: 20px;
        color: #454B4E;
    }

    .fa-ellipsis {
        font-size: 25px;
    }

    .fa-star {
        color: $goldColor;
    }

    &__item {
        cursor: pointer;
    }

    &__navigation {
        display: flex;
        align-items: center;
        gap: 20px;
        margin-left: 10px;
    }

    &__content {
        display: flex;
        align-items: center;
        gap: 5px;
    }

    &__link {
        display: flex;
        align-items: center;
        height: 35px;
        gap: 5px;
        margin-right: 5px;
    }

    &__container {
        position: relative;
        display: flex;
        align-items: center;

        // &::before {
        //     position: absolute;
        //     content: '';
        //     z-index: 2;
        //     top: 0;
        //     right: 0;
        //     background-color: #fff;
        //     height: 100%;
        //     width: 1px;
        //     display: block;
        //     box-shadow: -2px 0 18px 20px #fff;

        // }
    }

    &__tree {
        display: flex;
        align-items: center;
        max-height: 60px;
        white-space: nowrap;
        position: relative;
        padding-right: 15px;
        padding-left: 10px;

        // &::-webkit-scrollbar {
        //     height: 5px;
        // }

        // &::-webkit-scrollbar-thumb {
        //     background-color: #fff;
        //     height: 4px;
        //     border-radius: 5px;

        // }

        a {
            text-decoration: none;
            color: $textColor;
            transition: 0.3s all ease 0s;

            &:hover {
                background-color: $hoverGray;
            }

            padding: 2px 4px;
            border-radius: 5px;
        }
    }

    &__divider {
        font-size: 15px;
        color: $lightGray;
    }
}

.page-navbar-menu {
    position: absolute;
    top: 50px;
    right: 20px;
    width: 300px;
    max-height: 80vh;
    z-index: 3;
    background-color: #fff;
    border-radius: 5px;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
    padding: 10px 0;
    overflow-y: auto;

    /* width */
    &::-webkit-scrollbar {
        width: 9px;
    }

    /* Track */
    &::-webkit-scrollbar-track {
    }

    /* Handle */
    &::-webkit-scrollbar-thumb {
        background: $scrollGray;
        border-radius: 5px;
    }

    &__container {
        padding: 0 10px;
        &:not(:last-child) {
            margin-bottom: 10px;
        }
    }

    &__item {
        color: $textColor;
        border-radius: 5px;
        padding: 5px 10px;
        transition: .3s all ease 0s;
        display: flex;
        align-items: center;
        
        i {
            margin-right: 10px;
        }

        &:hover {
            background-color: $hoverGray;
        }
    }

    &__divider {
        width: 100%;
        height: 1px;
        background-color: $hoverGray;
        margin: 10px 0;
    }
}

.page-navbar-tree-more {
    position: relative;
    z-index: 20;
    text-align: center;

    &:hover {
        background-color: $hoverGray;

        .page-navbar-tree-more__container {
            display: block;
        }
    }

    p {
        cursor: pointer;
        color: $textColor;
        transition: 0.3s all ease 0s;
        padding: 2px 4px;
        border-radius: 5px;
        width: 20px;

        &:hover {
            background-color: $hoverGray;
        }
    }

    &__container {
        display: none;
        position: absolute;
        z-index: 11;
        top: 20px;
        left: 0;
    }

    &__content {
        background-color: #fff;
        border-radius: 5px;
        box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
        padding: 10px;
        position: relative;
        top: 15px;
        overflow: auto;
    }

    &__item {
        margin-bottom: 5px;
        display: flex;
        align-items: center;
        gap: 10px;
        width: 100%;

        a {
            width: 100%;
            text-align: start;
        }

        .fa-arrow-right {
            color: $lightGray;
            font-size: 14px;
            margin-right: 5px;
        }
    }
}

.locked-page {
    color: $lightGray;
    display: flex;
    align-items: center;
    gap: 7px;
    cursor: pointer;
    padding: 4px;
    border: 1px solid $lightGray;
    height: 25px;
    border-radius: 5px;

    i {
        color: $lightGray;
        position: relative;
        top: -2px;
    }
}